import CourseItem from 'pages/column/components/course-item/course-item'
export default {
  name: 'course-column',
  props: {
    courses: {
      type: Array,
      defalut: ''
    }
  },
  data () {
    return {
      pour: '倒序',
      audioUrl: '',
      pause: false
    }
  },
  computed: {},
  components: {
    CourseItem
  },
  watch: {},
  methods: {
    play (url) {
      this.audioUrl = url
      // console.log(this.$refs.audio)
      // this.$refs.audio.play()
      // document.querySelectorAll('audio')[0].play()
    },
    invertedOrder () {
      if (this.pour === '倒序') {
        this.pour = '正序'
        this.courses.reverse()
      } else {
        this.pour = '倒序'
        this.courses.reverse()
      }
    }
  },
  created () {},
  mounted () {
  }
}



